.footer__wrapper{
    background-color: #094091;
    padding: 2rem 0;
    margin-top: 2rem;
    color: #fff;
}
.footer__cards{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}
.footer__card{
    height: 30rem;
    width: 30%;
}
.footer__logo{
    width: 100%;
}.footer__logo img{
    max-width: 15rem;
}
.footer__copy__right p{
    font-size: 1.6rem;
    line-height: 1.5;
}
.footer__social__media__link svg{
    width: 2.4rem;
    height: 2.4rem;
    color: #fff;
    margin-right: .5rem;
}
.footer__social__media__link svg:hover{
    cursor: pointer;
}
.footer__card__pages__p{
    font-size: 2.4rem;
}
.footer__card__ul{

   margin: 0;
   padding: 0;
}
.footer__card__ul li{
   list-style: none; 
   font-size: 1.6rem;
   margin-bottom: .5rem;

}
.footer__card__ul li:hover{
    cursor: pointer;
}
.habarlasmak{
    font-size: 2.4rem;
}
.address{
    font-size: 1.6rem;
    line-height: 1.5;
}
