.article-wrapper{
    width: 75%;
    padding: 2rem 1rem;
    background-color: #fff;
}
.article__image{
    width: 100%;
    height: 40rem;
    background-position: center;
    background-size: cover;
    border-radius: 1rem;
}

.article__info{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.article__views, .article__date{
    font-size: 1.2rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: 500;
    color: #919094;
}
.article__title, .article__content__full{
    font-size: 1.4rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: 500;
    letter-spacing: 1.2px;
    line-height: 1.5;
}
.article__title{
    font-size: 1.8rem;
    font-weight: 600;
}
.article__views{
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.article__views svg{
    margin-right: 1rem;
}
@media only screen and (max-width: 1040px) {
    .article-wrapper{
    	width: 75%;
    }
} 
@media only screen and (max-width: 840px) {
    .article-wrapper{
    	width: 75%;
    }
} 
@media only screen and (max-width: 520px) {
    .article-wrapper{
        width: 100%;
        margin :auto;
    }
} 
